import { gql } from "@apollo/client";


export const GET_ALL_PROVINCE = gql`
  query getAllProvince($district: String) {
    getAllProvince(district: $district) {
      death
      recovered
      confirmedCase
      deathToday
      recoveredToday
      confirmedCaseToday
      totalHospital
      totalQuarantine
      totalAffectedLocation
      totalAffectedLocationToday
      totalAffectedLocationClosed
      totalAffectedLocationClosedToday
      sampleTest
      sampleTestToday
      delta
      deltaToday
      totalAffectedPeople
      totalAffectedPeopleToday
    }
  }
`;

export const GET_DATA_FOR_MAP = gql`
  query getAllDistrictForMap {
    getAllDistrictForMap {
      death
      confirmedCase
      confirmedCaseToday
      deathToday
      recovered
      recoveredToday
      _id
  
    }
  }
`;

export const GET_DATA_FOR_GRAP = gql`
     query getDataForGrap{
        getDataForGrap{
            cases{
              x 
              y
            }
            deaths{
              x 
              y
            }
            recovered{
              x 
              y
            }
        }
  }
`;

export const FOR_BAR_GRAP = gql`
  query getDataForBarGraphTotal{
    getDataForBarGraphTotal{
      confirm 
      recovered
      deaths
     
    }
  }
` 

export const CREATE_ADDITIONAL_RECORD = gql`
mutation createAdditional(
    $district: String,
    $positive: Int,
    $positiveWomen: Int,
    $hospitalizing: Int,
    $hospitalizingWomen: Int,
    $death: Int,
    $deathWomen: Int,
    $recover: Int,
    $recoverWomen: Int,
    $sampleTest: Int,
    $sampleTestWomen: Int,
    $affected: Int,
    $affectedWomen: Int
){
  createAdditional(additionalData:{
   	district: $district
    positive: $positive
    positiveWomen: $positiveWomen
    hospitalizing: $hospitalizing
    hospitalizingWomen: $hospitalizingWomen
    death: $death
    deathWomen: $deathWomen
    recover: $recover
    recoverWomen: $recoverWomen
    sampleTest: $sampleTest
    sampleTestWomen: $sampleTestWomen
    affected: $affected
    affectedWomen: $affectedWomen
  }){
    success
    message
  }
}
`

export const UPDATE_ADDITIONAL_RECORD = gql`
mutation updateAdditional(
  $district: String,
  $positive: Int,
  $positiveWomen: Int,
  $hospitalizing: Int,
  $hospitalizingWomen: Int,
  $death: Int,
  $deathWomen: Int,
  $recover: Int,
  $recoverWomen: Int,
  $sampleTest: Int,
  $sampleTestWomen: Int,
  $affected: Int,
  $affectedWomen: Int
){
  updateAdditional(district:$district,updateData:{
    district: $district
    positive: $positive
    positiveWomen: $positiveWomen
    hospitalizing: $hospitalizing
    hospitalizingWomen: $hospitalizingWomen
    death: $death
    deathWomen: $deathWomen
    recover: $recover
    recoverWomen: $recoverWomen
    sampleTest: $sampleTest
    sampleTestWomen: $sampleTestWomen
    affected: $affected
    affectedWomen: $affectedWomen
  }){
    success
    message
  }
}
`

export const GET_ALL_ADDITIONAL_DATA = gql`
query allAdditionalData($district:String){
	allAdditionalData(district:$district){
    positive
    positiveWomen
    hospitalizing
    hospitalizingWomen
    death
    deathWomen
    recover
    recoverWomen
    sampleTest
    sampleTestWomen
    affected
    affectedWomen
  }
}
`
export const UPDATE_ADDITIONALTWO_RECORD = gql`
mutation updateAdditionalTwo(
  $affectedLocation: Int
  $newAffectedLocation: Int
  $notClosedLocation: Int
  $newNotClosedLocation: Int
  $closedLocation: Int
  $newClosedLocation: Int
  $openedLocation: Int
  $newOpenLocation: Int
  $notCoorporateLocation: Int
  $newNotCoorporateLocation: Int
  $totalInterviewed: Int
  $reachedPatientForInterview: Int
  $interviewChinese: Int
  $interviewChineseWomen: Int
  $interviewChineseToday: Int
  $interViewCambodian: Int
  $interViewCambodianWomen: Int
  $interViewCambodianToday: Int
  $interviewAndAdviceAffectedPeople: Int
  $interviewAndAdviceAffectedPeopleWomen: Int
  $foundTotalAffectedLocation: Int
  $fullFoundTotalAffectedLocation: Int
  $totalSampleTestLocation: Int
  $totalSampleTest: Int
  $totalSampleTestWomen: Int
){
  updateAdditionalTwo(updateData:{
    affectedLocation:$affectedLocation
    newAffectedLocation: $newAffectedLocation
    notClosedLocation: $notClosedLocation
    newNotClosedLocation: $newNotClosedLocation
    closedLocation: $closedLocation
    newClosedLocation: $newClosedLocation
    openedLocation: $openedLocation
    newOpenLocation: $newOpenLocation
    notCoorporateLocation: $notCoorporateLocation
    newNotCoorporateLocation: $newNotCoorporateLocation
    totalInterviewed: $totalInterviewed
    reachedPatientForInterview: $reachedPatientForInterview
    interviewChinese: $interviewChinese
    interviewChineseWomen: $interviewChineseWomen
    interviewChineseToday: $interviewChineseToday
    interViewCambodian: $interViewCambodian
    interViewCambodianWomen: $interViewCambodianWomen
    interViewCambodianToday: $interViewCambodianToday
    interviewAndAdviceAffectedPeople: $interviewAndAdviceAffectedPeople
    interviewAndAdviceAffectedPeopleWomen: $interviewAndAdviceAffectedPeopleWomen
    foundTotalAffectedLocation: $foundTotalAffectedLocation
    fullFoundTotalAffectedLocation: $fullFoundTotalAffectedLocation
    totalSampleTestLocation: $totalSampleTestLocation
    totalSampleTest: $totalSampleTest
    totalSampleTestWomen: $totalSampleTestWomen
  }){
    success
    message
  }
}
`